import React, {useState} from "react";
import styles from './Paginator.module.css'
import cn from 'classnames'

type PaginatorType = {
    totalItemsCount: number
    pageSize: number
    currentPage: number
    onPageChanged: (pageNumber: number) => void
    portionSize: number
    isFetching: boolean
}

export const Paginator = ({isFetching, totalItemsCount, pageSize, currentPage, onPageChanged, portionSize = 10}: PaginatorType) => {
    let pagesCount = Math.ceil(totalItemsCount / pageSize)
    let pages = []
    for (let i = 1; i <= pagesCount; i++) {
        pages.push(i)
    }

    let portionCount = Math.ceil(pagesCount/portionSize)
    let [portionNumber, setPortionNumber] = useState(1)
    let leftPortionPageNumber = (portionNumber - 1) * portionSize + 1
    let rightPortionPageNumber = (portionNumber * portionSize)

    return (
        <div className={`${styles.paginator} ${isFetching && styles.disable}`}>
            {portionNumber > 1 &&
            <button onClick={()=>{setPortionNumber(portionNumber - 1)}}>PREV</button>
            }
            {pages
                .filter(p=> p>= leftPortionPageNumber && p<=rightPortionPageNumber)
                .map((p, index) => {
                return <span
                    key={index}
                    className={ cn({[styles.selectedPage]: currentPage === p }, styles.pageNumber) }
                    onClick={() => {
                        onPageChanged(p)
                    }}>{p}</span>
            })}
            {portionCount > portionNumber &&
                <button onClick={()=>{setPortionNumber(portionNumber + 1)}}>NEXT</button>
            }
        </div>
    )
}